@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon.eot?vyb7aa');
  src:  url('../fonts/icomoon.eot?vyb7aa#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?vyb7aa') format('truetype'),
    url('../fonts/icomoon.woff?vyb7aa') format('woff'),
    url('../fonts/icomoon.svg?vyb7aa#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-arrow:before {
  content: "\e903";
}
.icon-star-off:before {
  content: "\e904";
}
.icon-star-on:before {
  content: "\e905";
}
.icon-cogwheel:before {
  content: "\e900";
}
.icon-radio:before {
  content: "\e901";
}
.icon-radio-chacked:before {
  content: "\e902";
}

